<template>
	<view class="content">
		<image class="logo" src="/static/logo.png"></image>
		<view class="text-area">

			<text class="title">{{title}}</text>

		</view>
		<button class="rec-btn" :type="recBtnType" @touchstart="startvoice" @touchend="endvoice">按说语话松开停止</button>
	</view>
</template>

<script>
	import permision from "@/js_sdk/wa-permission/permission.js"
	const recorderManager = uni.getRecorderManager();
	const innerAudioContext = uni.createInnerAudioContext();

	export default {
		data() {
			return {
				title: '语音识别DEMO',
				recBtnType: 'success',
				voicetext: "",
				msg: "",
				voicepath: ""
			}
		},
		onLoad() {
			this.initaudio()
		},
		methods: {
			async initaudio() {
				//注意此处必须为 await 因为会触发异步事件，手机上会弹出权限申请对话框处理完才能走下一步录音  
				let recordauth = await permision.requestAndroidPermission("android.permission.RECORD_AUDIO")
				console.log("判断有没有录音权限>>>>>>" + recordauth)
				if (recordauth == 1) {
					recorderManager.onStart((res) => {
						console.log("开始 录音>>>>>>>>>...")
					});
					recorderManager.onStop((res) => {
						console.log("recorderstop....res.tempFilePath>>>" + res.tempFilePath)
						this.voicepath = res.tempFilePath
						this.uploadvoicefile()
						// 使用uni.uploadFile上传到服务器上，此时是mp3格式  
					});

					recorderManager.onError((res) => {

						console.log('onError' + JSON.stringify(res));
					});
				}
			}, //initaudio 方法结束  
			startvoice() {

			},
			endvoice() {

			}
		}
	}
</script>

<style>
	.rec-btn {}

	.content {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}

	.logo {
		height: 200rpx;
		width: 200rpx;
		margin-top: 200rpx;
		margin-left: auto;
		margin-right: auto;
		margin-bottom: 50rpx;
	}

	.text-area {
		display: flex;
		justify-content: center;
		/* flex-direction: column; */
	}

	.title {
		font-size: 36rpx;
		color: #8f8f94;
	}
</style>